@import "../styles/variables";

.cart {
  display: flex;

  &__button {
    height: 25px;
    width: 24px;
    background-image: url("../img/cart-inactive.svg");
    outline: none;
    background-color: transparent;
    transition: background-image $transition-duration;
    border: transparent;
    cursor: pointer;

    &--active {
      background-image: url("../img/cart.svg");

      &:hover {
        background-image: url("../img/cart.svg");
      }
    }
  }

  &__count {
    margin-left: 8px;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    color: white;
    background-color: black;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
  }
}
