@import "../styles/mixins";

.header {
  @include main-container; 
  
  margin-top: 20px;
  margin-bottom: 60px;

  &__list {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    list-style: none;
  }

  &__item {
    &--card {
      position: absolute;
      right: 0;
    }
  }
}
